<template>
	<div class="detail">
		<div class="title">{{detail.title}}</div>
		<div class="content">{{detail.content}}</div>
		<div class="back" @click="back()">返回上级</div>
	</div>
</template>

<script>
import store from '@/store'

export default{
	name:'detail',
	store,
	computed:{
		detail(){
			return store.state.detail;
		}
	},
	methods:{
		back(){
			this.$router.push('/home/list');
		}
	}
}
</script>

<style>
.detail{
	padding: 20px 20px;
}
.detail .title{
	border-bottom: 1px solid #E1E1E1;
	padding: 0 0 10px 10px;
	font-size: 18px;
}
.detail .content{
	font-size: 15px;
	padding: 10px 0 10px 0;
	text-indent: 2em;
	text-align: justify;
}
.back{
	position: absolute;
	bottom: 20px;
	right: 20px;
	background: #42B983;
	color: #FFFFFF;
	padding: 5px;
	border-radius: 5px;
	font-size: 12px;
}
</style>